import Container from "../../components/Container/Container";
import Pagestyle from "../../components/Pagestyle/Pagestyle";
import Link from "next/link";

export default function cadreel() {
  return (
    <Container>
      <div className="page relative" tabIndex="0">
        <h1 className="project-heading h1-styled">Cad Showreel</h1>
        <h2 className="h2-styled">Logi Remote</h2>
        <img
          className="project-banner"
          alt="Logitech inspired wireless remote"
          src="https://res.cloudinary.com/carlhernek/image/upload/f_auto,q_auto,w_512/v1582198505/Website/img/CAD/logi_p59qc0.png"
          srcSet="https://res.cloudinary.com/carlhernek/image/upload/f_auto,q_70,w_256/v1582198505/Website/ImgCAD/logi_p59qc0.png 256w,
          https://res.cloudinary.com/carlhernek/image/upload/f_auto,q_70,w_512/v1582198505/Website/Img/CAD/logi_p59qc0.png 512w,
          https://res.cloudinary.com/carlhernek/image/upload/f_auto,q_70,w_1024/v1582198505/Website/Img/CAD/logi_p59qc0.png 1024w"
        ></img>

        <div className="lone-child-wrap">
          <p>Wireless speaker remote inspired by Logi keyboard with dial.</p>
          <p>Modeled in SolidWorks, Render in Keyshot.</p>
        </div>
        <Link href="/design">
          <a className="project-return">Back to Design</a>
        </Link>

        <Pagestyle />
      </div>
    </Container>
  );
}
